.container {
	background: linear-gradient(180deg, #0c0505 0%, #3a3535 47.92%, #5e5858 100%);
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
}

.andrei-capa {
	width: 100%;
	height: 1000px;
}

main {
	width: 65%;
	padding-right: 20px;
	margin-top: 50px;
}

main h2 {
	color: #ffffff;
	border-bottom: 1px solid #6a6363;
	padding-bottom: 24px;
	margin-bottom: 12px;
}

.container-cards {
	display: flex;
	justify-content: space-between;
	flex-wrap: wrap;
}